html,
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --bg: #242526;
  --bg-accent: #484a4d;
  --text-color: #dadce1;
  --nav-size: 60px;
  --border: 1px solid #474a4d;
  --border-radius: 8px;
  --speed: 500ms;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  color: #dadce1;
  color: var(--text-color);
  text-decoration: none;
}















.grid-container {
    width: 100%;
    align-items: center;
    justify-content: center;
    display: flex;
}
  
.album-cover-grid{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(4, 1fr); 
    grid-gap: 30px; 
    gap: 30px;
    max-width: 900px;
}

@media (max-width: 900px) {
    .album-cover-grid {
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(5, 1fr);
    }
  }
  
@media (max-width: 730px) {
    .album-cover-grid {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(7, 1fr);
    }
}

 
@media (max-width: 550px) {
    .album-cover-grid {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(20, 1fr);
    }
}

.album-cover{
    width: 100%;
    height: 100%;
    max-width: 152px;
    max-height: 152px;
    border-radius: 5%;
    object-fit: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}

.album-cover:hover{
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
}

@media (max-width: 550px) {
    .album-cover {
    max-width: 200px;
    max-height: 200px;
    }
}
.popup{
    position: fixed;
    display: flex;
    width: 200px;
    height: 150px;
    z-index: 9998;
    background-color: #242526;
    align-items: center;
    justify-content: center;
    border-radius: 12.5px;
    color: #ffffff;
    text-align: center;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 1rem;
}

.popup-container{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.account-container{
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 50%;
    top: 25%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

#popup-button{
    position: absolute;
    left: 50%;
    bottom: 0;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.navbar {
  width: 100%;
  height: var(--nav-size);
  background-color: var(--bg);
  -webkit-padding-start: 0 1rem;
          padding-inline-start: 0 1rem;
  border-bottom: var(--border);
}

.navbar-nav{
  max-width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-left: 10px;
}

.nav-item{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
}

.dropdown{
  position: absolute;
  top: 58px;
  width: 300px;
  -webkit-transform: translateX(45%);
          transform: translateX(45%);
  background-color: var(--bg);
  border: var(--border);
  border-radius: var(--border-radius);
  padding: 1rem;
  overflow: hidden;
  z-index: 9999;
}
 
@media (max-width: 576px) {
  .dropdown {
  width: 200px;
  }
}

.dropdown-button{
  --button-size: calc(var(--nav-size) * 0.5);
  width: 100%;
  float: left;
  margin-right: 20px;
  white-space: nowrap;
  height: var(--button-size);
  border-radius: 5%;
  padding: 5px;
  margin: 2px;
  display: flex;
  align-items: center;
  justify-content: left;
  transition: -webkit-filter 300ms;
  transition: filter 300ms;
  transition: filter 300ms, -webkit-filter 300ms;
}

.dropdown-button:hover{
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}

.menu-item{
  height: 30px;
  display: flex;
  align-items: center;
  border-radius: var(--border-radius);
  transition: background var(--speed);
  padding: 0.5rem
}

.menu-item:hover{
  background-color: #525357;
}

.icon-button{
  --button-size: calc(var(--nav-size) * 0.5);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  float: left;
  margin-right: 5px;
  white-space: nowrap;
  height: var(--button-size);
  background-color: #484a4d;
  border-radius: 10%;
  padding: 5px;
  margin: 2px;
  display: flex;
  align-items: center;
  min-width: 50px;
  justify-content: center;
  transition: -webkit-filter 300ms;
  transition: filter 300ms;
  transition: filter 300ms, -webkit-filter 300ms;
}
  
.icon-button:hover{
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}

.login-button{
  --button-size: calc(var(--nav-size) * 0.5);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  float: left;
  margin-right: 5px;
  white-space: nowrap;
  height: var(--button-size);
  background-color: #484a4d;
  border-radius: 5%;
  padding: 5px;
  margin: 2px;
  display: flex;
  align-items: center;
  min-width: 50px;
  justify-content: center;
  transition: -webkit-filter 300ms;
  transition: filter 300ms;
  transition: filter 300ms, -webkit-filter 300ms;
}

.login-button:hover{
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}

.display-container{
  width: 100%;
  height: var(--nav-size);
  justify-content: center;
  align-items: center;
  display: flex;
}

.display{
  /* max-width: 480px;
  min-width: 320px; */
  height: 4.5em;
  font-size: .75em;
  border-radius: 1em;
  padding-left: 1em;
  padding-right: 1em;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
  background-color: #8f939a;
}

@media (max-width: 369px) {

  .display-container{
    height: calc(var(--nav-size)*2);
  }
  .display{
    max-width: 240px;
    min-width: none;
    padding: auto;
    height: 90px;
    flex-direction: column-reverse;
    padding-left: 0;
    padding-right: 0;
  }
  .display-left{
    height: 40%;
    left: 50%;
    -webkit-transform: translate(-30%, 0%);
            transform: translate(-30%, 0%);
  }
  .display-right{
    height: 60%;
  }
  .progress{
    bottom: 2em;
  }
}

.display-left{
  display: flex;
  width: 50%;
}

.display-right{
  display: flex;
  width: 50%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* overflow-x: hidden; */
}

